'use client'

import { useEffect } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { BookOpen, Clock, Award, TrendingUp, Play, LogOut } from 'lucide-react'
import { useAuth } from '@/lib/auth-context'

export default function DashboardPage() {
  const { user, isLoading, logout } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login')
    }
  }, [user, isLoading, router])

  if (isLoading) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <div className="text-center">
          <div className="mx-auto mb-4 size-12 animate-spin rounded-full border-4 border-primary border-t-transparent"></div>
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </div>
    )
  }

  if (!user) {
    return null
  }

  const enrolledCourses = [
    {
      id: 'solar-energy-installation',
      title: 'Solar Energy Installation',
      category: 'Renewable Energy',
      progress: 35,
      thumbnail: '/placeholder.svg?key=solar-thumb',
      nextLesson: 'Electrical Fundamentals',
      totalLessons: 16,
      completedLessons: 6,
      instructor: 'Fatou Jallow'
    },
    {
      id: 'ict-digital-skills',
      title: 'ICT & Digital Skills',
      category: 'Technology',
      progress: 12,
      thumbnail: '/placeholder.svg?key=ict-thumb',
      nextLesson: 'Introduction to Programming',
      totalLessons: 24,
      completedLessons: 3,
      instructor: 'Modou Ceesay'
    }
  ]

  const recentActivity = [
    { type: 'completed', title: 'What is Solar Energy?', course: 'Solar Energy Installation', date: '2 hours ago' },
    { type: 'completed', title: 'Types of Solar Systems', course: 'Solar Energy Installation', date: '3 hours ago' },
    { type: 'enrolled', title: 'ICT & Digital Skills', course: 'ICT & Digital Skills', date: '1 day ago' }
  ]

  const stats = [
    { label: 'Courses Enrolled', value: '2', icon: BookOpen },
    { label: 'Hours Learned', value: '24', icon: Clock },
    { label: 'Certificates', value: '0', icon: Award },
    { label: 'Progress', value: '23%', icon: TrendingUp }
  ]

  return (
    <div className="flex min-h-screen flex-col">
      {/* Header */}
      <header className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 sticky top-0 z-50">
        <div className="container mx-auto flex h-20 items-center justify-between px-4">
          <Link href="/" className="flex items-center gap-2">
            <Image
              src="/logo.png"
              alt="EmpowerTVET"
              width={300}
              height={90}
              className="h-16 w-auto"
              priority
            />
          </Link>
          <nav className="hidden items-center gap-6 md:flex">
            <Link href="/dashboard" className="text-sm font-medium text-primary">
              Dashboard
            </Link>
            <Link href="/lms" className="text-sm font-medium transition-colors hover:text-primary">
              Courses
            </Link>
            <Link href="/portal" className="text-sm font-medium transition-colors hover:text-primary">
              Portal
            </Link>
          </nav>
          <div className="flex items-center gap-3">
            <div className="hidden items-center gap-3 sm:flex">
              <div className="size-10 rounded-full bg-primary/10 flex items-center justify-center">
                <span className="font-medium text-primary">
                  {user.firstName[0]}{user.lastName[0]}
                </span>
              </div>
              <div className="text-sm">
                <div className="font-medium">{user.firstName} {user.lastName}</div>
                <div className="text-xs text-muted-foreground capitalize">{user.role}</div>
              </div>
            </div>
            <Button variant="ghost" size="icon" onClick={logout} title="Logout">
              <LogOut className="size-4" />
            </Button>
          </div>
        </div>
      </header>

      <div className="flex-1 bg-muted/30">
        <div className="container mx-auto px-4 py-8">
          {/* Welcome Section */}
          <div className="mb-8">
            <h1 className="mb-2 text-3xl font-bold">Welcome back, {user.firstName}!</h1>
            <p className="text-muted-foreground">Continue your learning journey</p>
          </div>

          {/* Stats Grid */}
          <div className="mb-8 grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
            {stats.map((stat, index) => {
              const Icon = stat.icon
              return (
                <Card key={index}>
                  <CardContent className="flex items-center gap-4 p-6">
                    <div className="flex size-12 items-center justify-center rounded-full bg-primary/10">
                      <Icon className="size-6 text-primary" />
                    </div>
                    <div>
                      <div className="text-2xl font-bold">{stat.value}</div>
                      <div className="text-sm text-muted-foreground">{stat.label}</div>
                    </div>
                  </CardContent>
                </Card>
              )
            })}
          </div>

          <Tabs defaultValue="courses" className="w-full">
            <TabsList>
              <TabsTrigger value="courses">My Courses</TabsTrigger>
              <TabsTrigger value="activity">Recent Activity</TabsTrigger>
            </TabsList>

            <TabsContent value="courses" className="mt-6">
              <div className="grid gap-6 lg:grid-cols-2">
                {enrolledCourses.map((course) => (
                  <Card key={course.id} className="overflow-hidden">
                    <div className="flex flex-col sm:flex-row">
                      <div className="aspect-video w-full overflow-hidden bg-muted sm:w-48">
                        <img
                          src={course.thumbnail || "/placeholder.svg"}
                          alt={course.title}
                          className="size-full object-cover"
                        />
                      </div>
                      <div className="flex flex-1 flex-col p-6">
                        <div className="mb-2">
                          <Badge variant="secondary" className="mb-2">
                            {course.category}
                          </Badge>
                          <h3 className="mb-1 text-lg font-semibold">{course.title}</h3>
                          <p className="text-sm text-muted-foreground">by {course.instructor}</p>
                        </div>
                        <div className="mt-auto space-y-3">
                          <div>
                            <div className="mb-2 flex items-center justify-between text-sm">
                              <span className="text-muted-foreground">Progress</span>
                              <span className="font-medium">{course.progress}%</span>
                            </div>
                            <Progress value={course.progress} className="h-2" />
                          </div>
                          <div className="flex items-center justify-between text-sm text-muted-foreground">
                            <span>{course.completedLessons} of {course.totalLessons} lessons</span>
                          </div>
                          <Button className="w-full" asChild>
                            <Link href={`/learn/${course.id}`}>
                              <Play className="mr-2 size-4" />
                              Continue Learning
                            </Link>
                          </Button>
                        </div>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>

              <div className="mt-8 text-center">
                <Button variant="outline" size="lg" asChild>
                  <Link href="/lms">Browse More Courses</Link>
                </Button>
              </div>
            </TabsContent>

            <TabsContent value="activity" className="mt-6">
              <Card>
                <CardHeader>
                  <CardTitle>Recent Activity</CardTitle>
                </CardHeader>
                <CardContent>
                  <ul className="space-y-4">
                    {recentActivity.map((activity, index) => (
                      <li key={index} className="flex items-start gap-3 border-b pb-4 last:border-0 last:pb-0">
                        <div className={`mt-1 size-2 shrink-0 rounded-full ${activity.type === 'completed' ? 'bg-green-500' : 'bg-blue-500'
                          }`} />
                        <div className="flex-1">
                          <div className="font-medium">{activity.title}</div>
                          <div className="text-sm text-muted-foreground">{activity.course}</div>
                        </div>
                        <div className="text-sm text-muted-foreground">{activity.date}</div>
                      </li>
                    ))}
                  </ul>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </div>
  )
}
